import React from "react";

export const NetworkTab =()=>{
    return (
        <header >
            <div className="flex w-full border-2 justify-between">

                <ul className="flex gap-3 p-5">
                    <li>Transfer</li>
                    <li>Refuel</li>
                    <li>Transaction</li>
                </ul>

                <select className="focus:outline-none p-0">
                    <option>BSC</option>
                    <option>ETH</option>
                    <option>MATIC</option>
                    <option>FANTOM</option>
                    <option>ARBITRUM</option>
                </select>
            </div>
        </header>
    )
}